rProps[\"functions\"];\n    callbacks: IProcessEditorProps[\"callbacks\"];\n}\n\nconst viewModel = container.resolve<IViewModel>(IViewModel);\nconst executor = container.resolve<IUseCaseExecutor>(IUseCaseExecutor);\n\nconst studio = container.resolve<IStudio>(IStudio);\nconst processEditorUtil = container.resolve<IProcessEditorUtil>(IProcessEditorUtil);\nconst props = defineProps<IProps>();\nconst renderKey = ref(\"\");\nconst disableFinishButton = ref(false);\nconst title = ref(\"Process Wizard\");\nconst wizardHeight = ref(\"700px\");\n\nlet initialized = ref(false);\n\nconst processModel = ref(processEditorUtil.getEmptyProcessWizardModel(\"_\"));\nconst processEditorState = ref(processEditorUtil.getEmptyProcessEditorState());\nconst currentModule = ref({} as IModule);\nconst modelsOfModule = ref({\n    screens: [],\n    bpmns: [],\n} as IModelsOfModule);\nconst processVariablesEditable = ref(true);\n\nprops.functions.getModel = async () => {\n    return { model: processModel.value, state: processEditorState.value };\n};\n\nprops.functions.setModel = async (model, module, state) => {\n    currentModule.value = module;\n    modelsOfModule.value = processEditorUtil.getModelsOfModule(module);\n    renderKey.value = CryptoHelper.CreateGuid();\n    processModel.value = model;\n    processEditorState.value = state;\n    initialized.value = true;\n    resizeWizard();\n};\n\nasync function handleNext() {\n    const stepper = processEditorState.value.stepper;\n    const selectedExistingBpmnFile = processModel.value.selectedExistingBpmnFile;\n    const currentPage = stepper.currentStep;\n    if (stepper.isTabValid) {\n        stepper.currentStep++;\n        stepper.isTabValid = false;\n        props.callbacks.modified({ state: true });\n    }\n    resizeWizard();\n    processVariablesEditable.value =\n        processModel.value.usingExistingBpmnFile && processEditorState.value.selectedExistingBpmn && stepper.currentStep == 1;\n\n    if (stepper.currentStep == 4) {\n        writeProcessModelDataToProcessVariablesTable();\n    } else if (currentPage == 4) {\n        writeProcessVariablesTableDataToProcessModel();\n    }\n}\n\nfunction handleBack() {\n    if (processEditorState.value.stepper!.currentStep == 4) {\n        writeProcessVariablesTableDataToProcessModel();\n    }\n    processEditorState.value.stepper!.currentStep--;\n    processEditorState.value.stepper!.isTabValid = false;\n    processVariablesEditable.value =\n        processModel.value.usingExistingBpmnFile &&\n        processEditorState.value.selectedExistingBpmn &&\n        processEditorState.value.stepper.currentStep == 1;\n    props.callbacks.modified({ state: true });\n    resizeWizard();\n}\n\nfunction handleSave() {\n    const processModelItem = viewModel.studio.currentItem!;\n\n    if (processModel.value.processState == \"CREATE\") {\n        const bpmnFile = processEditorUtil.generateBpmnFile(processModel.value, processEditorState.value.processFileName);\n        executor.execute(\n            async () => {\n                const createdItem = await executor.executeUseCase<ICreateModel>(ICreateModel, {\n                    name: processModel.value.processFlowName || processEditorState.value.processFileName,\n                    parentObjectId: currentModule.value.ID,\n                    modelType: \"bpmn\",\n                    modelBody: [{ key: \"bpmn\", model: bpmnFile }],\n                    modelAdditionals: {},\n                });\n                processModel.value.processState = \"UPDATE\";\n                processModel.value.selectedExistingBpmnFile.ID = createdItem.ID;\n                modelsOfModule.value.bpmns.push(createdItem);\n                disableFinishButton.value = true;\n                await studio.save(processModelItem);\n                processEditorState.value.stepper.currentStep = 1;\n                resizeWizard();\n                studio.openItem(createdItem, { fromHistory: false, noFocus: true });\n            },\n            { loading: true }\n        );\n    } else {\n        disableFinishButton.value = true;\n        executor.execute(\n            async () => {\n                disableFinishButton.value = true;\n                await studio.save(processModelItem);\n            },\n            { loading: true }\n        );\n    }\n    props.callbacks.modified({ state: true });\n}\n\nfunction handleDiscard() {\n    executor.execute(\n            async () => {\n                await studio.release(viewModel.studio.currentItem!);\n            },\n            { loading: true }\n        );\n}\n\nfunction resizeWizard() {\n    if (\n        processEditorState.value.stepper.currentStep == 4 ||\n        processEditorState.value.stepper.currentStep == 5 ||\n        !viewModel.studio.currentItem?.checkedOut\n    ) {\n        wizardHeight.value = \"886px\";\n    } else {\n        wizardHeight.value = \"700px\";\n    }\n}\n\nfunction writeProcessModelDataToProcessVariablesTable() {\n    if (!processModel.value.usingExistingBpmnFile) {\n        if (\n            processModel.value.processType == \"double_approval\" ||\n            processModel.value.processType == \"triple_approval\" ||\n            processModel.value.processType == \"quadruple_approval\" ||\n            processModel.value.processType == \"quintuple_approval\"\n        ) {\n            if (!processEditorState.value.processVariablesTableRows.some((row) => row.parameter == \"supportingForm2\"))\n                processEditorState.value.processVariablesTableRows.push({\n                    parameter: \"supportingForm2\",\n                    currentValue: \"\",\n                    defaultValue: \"\",\n                    dataType: \"Screen\",\n                    defaultDataType: \"Screen\",\n                    isDeletable: false,\n                });\n            if (!processEditorState.value.processVariablesTableRows.some((row) => row.parameter == \"checkerRoles2\"))\n                processEditorState.value.processVariablesTableRows.push({\n                    parameter: \"checkerRoles2\",\n                    currentValue: processEditorState.value.processName + \".secondChecker\",\n                    defaultValue: processEditorState.value.processName + \".secondChecker\",\n                    dataType: \"String\",\n                    defaultDataType: \"String\",\n                    isDeletable: false,\n                });\n        } \n        if (\n            processModel.value.processType == \"triple_approval\" ||\n            processModel.value.processType == \"quadruple_approval\" ||\n            processModel.value.processType == \"quintuple_approval\") {\n            if (!processEditorState.value.processVariablesTableRows.some((row) => row.parameter == \"supportingForm3\"))\n                processEditorState.value.processVariablesTableRows.push({\n                    parameter: \"supportingForm3\",\n                    currentValue: \"\",\n                    defaultValue: \"\",\n                    dataType: \"Screen\",\n                    defaultDataType: \"Screen\",\n                    isDeletable: false,\n                });\n            if (!processEditorState.value.processVariablesTableRows.some((row) => row.parameter == \"checkerRoles3\"))\n                processEditorState.value.processVariablesTableRows.push({\n                    parameter: \"checkerRoles3\",\n                    currentValue: processEditorState.value.processName + \".thirdChecker\",\n                    defaultValue: processEditorState.value.processName + \".thirdChecker\",\n                    dataType: \"String\",\n                    defaultDataType: \"String\",\n                    isDeletable: false,\n                });\n        } \n        if (\n            processModel.value.processType == \"quadruple_approval\" ||\n            processModel.value.processType == \"quintuple_approval\") {\n            if (!processEditorState.value.processVariablesTableRows.some((row) => row.parameter == \"supportingForm4\"))\n                processEditorState.value.processVariablesTableRows.push({\n                    parameter: \"supportingForm4\",\n                    currentValue: \"\",\n                    defaultValue: \"\",\n                    dataType: \"Screen\",\n                    defaultDataType: \"Screen\",\n                    isDeletable: false,\n                });\n            if (!processEditorState.value.processVariablesTableRows.some((row) => row.parameter == \"checkerRoles4\"))\n                processEditorState.value.processVariablesTableRows.push({\n                    parameter: \"checkerRoles4\",\n                    currentValue: processEditorState.value.processName + \".fourthChecker\",\n                    defaultValue: processEditorState.value.processName + \".fourthChecker\",\n                    dataType: \"String\",\n                    defaultDataType: \"String\",\n                    isDeletable: false,\n                });\n        } \n        if (\n            processModel.value.processType == \"quintuple_approval\") {\n            if (!processEditorState.value.processVariablesTableRows.some((row) => row.parameter == \"supportingForm5\"))\n                processEditorState.value.processVariablesTableRows.push({\n                    parameter: \"supportingForm5\",\n                    currentValue: \"\",\n                    defaultValue: \"\",\n                    dataType: \"Screen\",\n                    defaultDataType: \"Screen\",\n                    isDeletable: false,\n                });\n            if (!processEditorState.value.processVariablesTableRows.some((row) => row.parameter == \"checkerRoles5\"))\n                processEditorState.value.processVariablesTableRows.push({\n                    parameter: \"checkerRoles5\",\n                    currentValue: processEditorState.value.processName + \".fifthChecker\",\n                    defaultValue: processEditorState.value.processName + \".fifthChecker\",\n                    dataType: \"String\",\n                    defaultDataType: \"String\",\n                    isDeletable: false,\n                });\n        } \n    }\n    processEditorState.value.processVariablesTableRows.forEach((row) => {\n        switch (row.parameter) {\n            case \"isRunAfterApprovalEnable\":\n                row.currentValue = processModel.value.runAfterApprovalDefinition.isRunAfterApprovalEnable;\n                row.dataType = row.defaultDataType;\n                break;\n            case \"runAfterApprovalApi\":\n                row.currentValue = processModel.value.runAfterApprovalDefinition.runAfterApprovalApi;\n                row.dataType = row.defaultDataType;\n                break;\n            case \"runAfterApprovalMethod\":\n                row.currentValue = processModel.value.runAfterApprovalDefinition.runAfterApprovalMethod;\n                row.dataType = row.defaultDataType;\n                break;\n            case \"isRunAfterChangeAdvisedEnable\":\n                row.currentValue = processModel.value.runAfterChangeAdvisedDefinition.isRunAfterChangeAdvisedEnable;\n                row.dataType = row.defaultDataType;\n                break;\n            case \"runAfterChangeAdvisedApi\":\n                row.currentValue = processModel.value.runAfterChangeAdvisedDefinition.runAfterChangeAdvisedApi;\n                row.dataType = row.defaultDataType;\n                break;\n            case \"runAfterChangeAdvisedMethod\":\n                row.currentValue = processModel.value.runAfterChangeAdvisedDefinition.runAfterChangeAdvisedMethod;\n                row.dataType = row.defaultDataType;\n                break;\n            case \"isRunAfterRejectedEnable\":\n                row.currentValue = processModel.value.runAfterRejectedDefinition.isRunAfterRejectedEnable;\n                row.dataType = row.defaultDataType;\n                break;\n            case \"runAfterRejectedApi\":\n                row.currentValue = processModel.value.runAfterRejectedDefinition.runAfterRejectedApi;\n                row.dataType = row.defaultDataType;\n                break;\n            case \"runAfterRejectedMethod\":\n                row.currentValue = processModel.value.runAfterRejectedDefinition.runAfterRejectedMethod;\n                row.dataType = row.defaultDataType;\n                break;\n            case \"useHierarchicalProcess\":\n                row.currentValue = processModel.value.processMakerDefinition.useHierarchicalProcess;\n                row.dataType = row.defaultDataType;\n                break;\n            case \"ignoreLevelForDiffInstUnit\":\n                row.currentValue = processModel.value.processMakerDefinition.ignoreLevelForDiffInstUnit;\n                row.dataType = row.defaultDataType;\n                break;\n            case \"instUnitHierarchy\":\n                row.currentValue = processModel.value.processMakerDefinition.instUnitHierarchy;\n                row.dataType = row.defaultDataType;\n                break;\n            case \"supportingForm\":\n                row.currentValue = processModel.value.processCheckerDefinition.firstApproverScreenID;\n                row.dataType = row.defaultDataType;\n                break;\n            case \"supportingForm2\":\n                row.currentValue = processModel.value.processCheckerDefinition.secondApproverScreenID;\n                row.dataType = row.defaultDataType;\n                break;\n            case \"supportingForm3\":\n                row.currentValue = processModel.value.processCheckerDefinition.thirdApproverScreenID;\n                row.dataType = row.defaultDataType;\n                break;\n            case \"supportingForm4\":\n                row.currentValue = processModel.value.processCheckerDefinition.fourthApproverScreenID;\n                row.dataType = row.defaultDataType;\n                break;\n            case \"supportingForm5\":\n                row.currentValue = processModel.value.processCheckerDefinition.fifthApproverScreenID;\n                row.dataType = row.defaultDataType;\n                break;\n            case \"reviewForm\":\n                row.currentValue = processModel.value.reviewScreenID;\n                row.dataType = row.defaultDataType;\n                break;\n            default:\n                break;\n        }\n    });\n}\n\nfunction writeProcessVariablesTableDataToProcessModel() {\n    processEditorState.value.processVariablesTableRows.forEach((row) => {\n        switch (row.parameter) {\n            case \"isRunAfterApprovalEnable\":\n                if (row.dataType == \"Boolean\") processModel.value.runAfterApprovalDefinition.isRunAfterApprovalEnable = row.currentValue as boolean;\n                break;\n            case \"runAfterApprovalApi\":\n                if (row.dataType == \"String\") processModel.value.runAfterApprovalDefinition.runAfterApprovalApi = row.currentValue as string;\n                break;\n            case \"runAfterApprovalMethod\":\n                if (row.dataType == \"Http Method\") processModel.value.runAfterApprovalDefinition.runAfterApprovalMethod = row.currentValue as string;\n                break;\n            case \"isRunAfterChangeAdvisedEnable\":\n                if (row.dataType == \"Boolean\")\n                    processModel.value.runAfterChangeAdvisedDefinition.isRunAfterChangeAdvisedEnable = row.currentValue as boolean;\n                break;\n            case \"runAfterChangeAdvisedApi\":\n                if (row.dataType == \"String\")\n                    processModel.value.runAfterChangeAdvisedDefinition.runAfterChangeAdvisedApi = row.currentValue as string;\n                break;\n            case \"runAfterChangeAdvisedMethod\":\n                if (row.dataType == \"Http Method\")\n                    processModel.value.runAfterChangeAdvisedDefinition.runAfterChangeAdvisedMethod = row.currentValue as string;\n                break;\n            case \"isRunAfterRejectedEnable\":\n                if (row.dataType == \"Boolean\") processModel.value.runAfterRejectedDefinition.isRunAfterRejectedEnable = row.currentValue as boolean;\n                break;\n            case \"runAfterRejectedApi\":\n                if (row.dataType == \"String\") processModel.value.runAfterRejectedDefinition.runAfterRejectedApi = row.currentValue as string;\n                break;\n            case \"runAfterRejectedMethod\":\n                if (row.dataType == \"Http Method\") processModel.value.runAfterRejectedDefinition.runAfterRejectedMethod = row.currentValue as string;\n                break;\n            case \"useHierarchicalProcess\":\n                if (row.dataType == \"Boolean\") processModel.value.processMakerDefinition.useHierarchicalProcess = row.currentValue as boolean;\n                break;\n            case \"ignoreLevelForDiffInstUnit\":\n                if (row.dataType == \"Boolean\") processModel.value.processMakerDefinition.ignoreLevelForDiffInstUnit = row.currentValue as boolean;\n                break;\n            case \"instUnitHierarchy\":\n                if (row.dataType == \"String\" && (row.currentValue == \"SAME\" || row.currentValue == \"PARENT\"))\n                    processModel.value.processMakerDefinition.instUnitHierarchy = row.currentValue as string;\n                break;\n            case \"supportingForm\":\n                if (row.dataType == \"Screen\") processModel.value.processCheckerDefinition.firstApproverScreenID = row.currentValue as string;\n                break;\n            case \"supportingForm2\":\n                if (row.dataType == \"Screen\") processModel.value.processCheckerDefinition.secondApproverScreenID = row.currentValue as string;\n                break;\n            case \"supportingForm3\":\n                if (row.dataType == \"Screen\") processModel.value.processCheckerDefinition.thirdApproverScreenID = row.currentValue as string;\n                break;\n            case \"supportingForm4\":\n                if (row.dataType == \"Screen\") processModel.value.processCheckerDefinition.fourthApproverScreenID = row.currentValue as string;\n                break;\n            case \"supportingForm5\":\n                if (row.dataType == \"Screen\") processModel.value.processCheckerDefinition.fifthApproverScreenID = row.currentValue as string;\n                break;\n            case \"reviewForm\":\n                if (row.dataType == \"Screen\") processModel.value.reviewScreenID = row.currentValue as string;\n                break;\n            default:\n                break;\n        }\n    });\n}\n</script>\n<template>\n    <div class=\"process-editor\" :style=\"'height:' + wizardHeight\" v-if=\"initialized\" :key=\"renderKey\">\n        <ProcessEditorHeader :state=\"processEditorState\" :title=\"title\" :process-state=\"processModel.processState\" />\n        <div v-if=\"!viewModel.studio.currentItem?.checkedOut\">\n            <SummaryTab :model=\"processModel\" :screens=\"modelsOfModule.screens\" :variables=\"processEditorState.processVariablesTableRows\" />\n        </div>\n        <div v-else>\n            <ScreenSelectionTab\n                v-if=\"processEditorState.stepper.currentStep === 1\"\n                :model=\"processModel\"\n                :models-of-module=\"modelsOfModule\"\n                :state=\"processEditorState\"\n                :modified=\"props.callbacks.modified\"\n            />\n            <ServiceCallConfigurationTab\n                v-if=\"processEditorState.stepper.currentStep === 2\"\n                :model=\"processModel\"\n                :models-of-module=\"modelsOfModule\"\n                :state=\"processEditorState\"\n                :modified=\"props.callbacks.modified\"\n            />\n            <AdvancedSettingsTab\n                v-if=\"processEditorState.stepper.currentStep === 3\"\n                :model=\"processModel\"\n                :state=\"processEditorState