nt\" :style=\"{ height: processScreensContentHeight }\">\n                    <div  v-for=\"(v, k) in processScreens\">\n                        <div class=\"sub-content\" v-if = \"v!='-'\">\n                            <label class=\"ct-header\">{{ k }}</label><br />\n                            <i class=\"mdi mdi-palette ct-icon\"></i>\n                            <label class=\"ct-text\">{{ v }}</label>\n                        </div>\n                    </div>\n                </div>\n            </div>\n            <div>\n                <label class=\"child-title\">Service Call</label>\n                <div class=\"service-call-content\">\n                    <br /><label class=\"service-call-subtitle\">After All Approvals Done</label><br />\n                    <div class=\"sub-content\" v-for=\"(v, k) in serviceCallAfterAllApprovalsDone\">\n                        <label class=\"ct-header\">{{ k }}</label><br />\n                        <label class=\"ct-text-without-icon\">{{ v }}</label>\n                    </div>\n                    <hr class= \"line\"/>\n                    <label class=\"service-call-subtitle\">After Process Rejected</label><br />\n                    <div class=\"sub-content\" v-for=\"(v, k) in serviceCallAfterProcessRejected\">\n                        <label class=\"ct-header\">{{ k }}</label><br />\n                        <label class=\"ct-text-without-icon\">{{ v }}</label>\n                    </div>\n                    <hr class= \"line\"/>\n                    <label class=\"service-call-subtitle\">After Changes Advised</label><br />\n                    <div class=\"sub-content\" v-for=\"(v, k) in serviceCallAfterChangesAdvised\">\n                        <label class=\"ct-header\">{{ k }}</label><br />\n                        <label class=\"ct-text-without-icon\">{{ v }}</label>\n                    </div>\n                </div>\n            </div>\n            <div>\n                <label class=\"child-title\">Advanced Settings</label>\n                <div class=\"advanced-settings-content\">\n                    <div class=\"sub-content\" v-for=\"(v, k) in advancedSettings\">\n                        <label class=\"ct-header\">{{ k }}</label><br />\n                        <label class=\"ct-text-without-icon\">{{ v }}</label>\n                    </div>\n                </div>\n            </div>\n        </div>\n\n        <div class=\"content\" v-else-if=\"selectedTab == '2'\">\n            <div class=\"data-table\">\n                <div class=\"table-header\">\n                    <span v-for=\"header in tableHeaders\" :class=\"'table-header-text header-' + header\"> {{\n                            localization.get(header)\n                    }} </span>\n                </div>\n                <div class=\"table-body\">\n                    <div class=\"table-row\" :style=\"'background-color:' + (index % 2 == 0 ? 'white' : '#F5F5FA')\"\n                        v-for=\"(row, index) in tableRows\">\n                        <div class=\"row-parameter\">{{ row.parameter }}</div>\n\n                        <div class=\"row-data-type data-type\">{{ row.dataType }}</div>\n\n                        <div class=\"row-current-value current-value\">\n                            {{ screens.find((screen) => screen.ID == row.currentValue)?.name || row.currentValue || \"-\"\n                            }}\n                        </div>\n\n                        <div class=\"row-default-value\">{{ row.defaultValue || \"-\" }}</div>\n                    </div>\n                </div>\n            </div>\n        </div>\n    </div>\n</template>\n\n<script setup lang=\"ts\">\nimport { computed, ref } from \"vue\";\nimport { IProcessModel, ProcessVariable } from \"@stechquick/algae/lib/qCloudTemp/process\";\nimport { IModel } from \"../../../../../domain/model/models\";\nimport { ILocalization } from \"../../../../../domain/infrastructure/ILocalization\";\nimport { container } from \"../../../../../domain/core/diContainer\";\nimport { IProcessVariableRow } from '../../../../../domain/presentation/editors/IProcessEditorModel';\nimport { IViewModel } from \"../../../../../domain/viewModel/IViewModel\";\n\n\ninterface IProcessVariableProps {\n    model: IProcessModel;\n    screens: Array<IModel>;\n    variables: Array<IProcessVariableRow>;\n}\n\nconst viewModel = container.resolve<IViewModel>(IViewModel);\nconst summaryTitleVisibility = computed(() => viewModel.studio.currentItem?.checkedOut);\nconst selectedTab = ref(\"1\")\nconst tableHeaders = ref([\"parameter\", \"dataType\", \"currentValue\", \"defaultValue\", \"operations\"]);\nconst localization = container.resolve<ILocalization>(ILocalization);\nconst props = defineProps<IProcessVariableProps>();\nconst tableRows = ref(props.variables.sort((variable1, variable2) => variable1.parameter.localeCompare(variable2.parameter)));\n\nconst processScreens = ref({\n    \"Start Screen\": getScreenName(props.model.processMakerDefinition.makerScreenID) || \"-\",\n    \"1st Approver Screen\": getScreenName(props.model.processCheckerDefinition.firstApproverScreenID) || \"-\",\n    \"2nd Approver Screen\": getScreenName(props.model.processCheckerDefinition.secondApproverScreenID) || \"-\",\n    \"3nd Approver Screen\": getScreenName(props.model.processCheckerDefinition.thirdApproverScreenID) || \"-\",\n    \"4nd Approver Screen\": getScreenName(props.model.processCheckerDefinition.fourthApproverScreenID) || \"-\",\n    \"5nd Approver Screen\": getScreenName(props.model.processCheckerDefinition.fifthApproverScreenID) || \"-\",\n    \"Review Screen\": getScreenName(props.model.reviewScreenID) || \"-\"\n})\n\nconst serviceCallAfterAllApprovalsDone = ref({\n    \"Service URL\": props.model.runAfterApprovalDefinition.isRunAfterApprovalEnable ? props.model.runAfterApprovalDefinition.runAfterApprovalApi : \"-\",\n    \"Service HTTP Method\": props.model.runAfterApprovalDefinition.isRunAfterApprovalEnable ? props.model.ru