>\n    </div>\n    <div class=\"data-table\">\n        <div class=\"table-header\">\n            <span v-for=\"header in tableHeaders\" :class=\"'table-header-text header-' + header\"> {{\n                    localization.get(header)\n            }} </span>\n        </div>\n        <div class=\"table-body\">\n            <div class=\"table-row\" :style=\"'background-color:' + (index % 2 == 0 ? 'white' : '#F5F5FA')\"\n                v-for=\"(row, index) in tableRows\">\n                <div class=\"row-parameter\">{{ row.parameter }}</div>\n\n                <div v-if=\"!props.editable\" class=\"row-data-type data-type\">{{ row.dataType }}</div>\n                <div v-else class=\"row-data-type data-type-editable\">\n                    <select class=\"data-type-combo\" v-model=\"row.dataType\" @change=\"handleDataTypeChange(row)\">\n                        <option v-for=\"dataType in dataTypes\" :selected=\"dataType == row.dataType\">{{ dataType }}\n                        </option>\n                    </select>\n                </div>\n\n                <div v-if=\"!props.editable\" class=\"row-current-value current-value\">\n                    {{ screens.find((screen) => screen.ID == row.currentValue)?.name || row.currentValue || \"-\" }}\n                </div>\n                <div v-else class=\"row-current-value current-value-editable\">\n                    <input v-if=\"row.dataType == 'String' || row.dataType === 'Number'\"\n                        type='text' class=\"current-value-input\"\n                        @input=\"modifiedModel\"\n                        :id=\"row.parameter\" :name=\"row.parameter\" v-model=\"row.currentValue\"\n                        />\n                    <select v-if=\"row.dataType == 'Boolean'\" class=\"current-value-input\"\n                        v-model=\"row.currentValue\"\n                        @change=\"modifiedModel\"\n                        :style=\"'color:' + (String(row.currentValue).length == 0 ? '#A1A1A1' : 'black')\">\n                        <option value=\"\" disabled  :selected=\"row.currentValue == ''\">\n                            Select</option>\n                        <option :selected=\"!row.currentValue\" style=\"color: black !important\">false</option>\n                        <option :selected=\"!!row.currentValue\" style=\"color: black !important\">true</option>\n                    </select>\n                    <select v-if=\"row.dataType == 'Screen'\" class=\"current-value-input\"\n                        @change=\"handleScreen($event, row)\"\n                        :style=\"'color:' + (screens.some((screen) => screen.ID == row.currentValue) ? 'black' : '#A1A1A1')\">\n                        <option value=\"\" disabled :selected=\"!screens.some((screen) => screen.ID == row.currentValue)\">\n                            Select</option>\n                        <option v-for=\"screen in props.screens\" :id=\"screen.ID\"\n                            :selected=\"row.currentValue == screen.ID\" style=\"color: black !important\">\n                            {{ screen.name }}\n                        </option>\n                    </select>\n                    <select v-if=\"row.dataType == 'Http Method'\" class=\"current-value-input\"\n                        v-model=\"row.currentValue\"\n                        @change=\"modifiedModel\"\n                        :style=\"'color:' + (restMethods.some((method) => method == row.currentValue) ? ' black' : '#A1A1A1')\"\n                        >\n                        <option value=\"\" disabled :selected=\"!restMethods.some((method) => method == row.currentValue)\">\n                            Select</option>\n                        <option v-for=\"restMethod in restMethods\" style=\"color: black !important\">{{ restMethod }}\n                        </option>\n                    </select>\n                </div>\n\n                <div class=\"row-default-value\">{{ String(row.defaultValue) || \"-\" }}</div>\n                <div v-if=\"props.editable\">\n                    <i v-if=\"row.isDeletable\" class=\"mdi mdi-delete-outline row-operation\"\n                        @click=\"deleteVariable(row, index)\"></i>\n                    <i v-else class=\"mdi mdi-arrow-u-left-top row-operation\" @click=\"returnToDefaultValue(row)\"></i>\n                </div>\n            </div>\n        </div>\n    </div>\n</template>\n\n<script setup lang=\"ts\">\nimport { ref, onBeforeMount } from 'vue';\nimport { ILocalization } from \"../../../../../domain/infrastructure/ILocalization\";\nimport { container } from \"../../../../../domain/core/diContainer\";\nimport { IModel } from \"../../../../../domain/model/models\";\nimport { ModifiedCallbackType } from \"../../../../../domain/model/editors/modifiedCallbackType\";\nimport { IProcessEditorState, IProcessVariableRow } from '../../../../../domain/presentation/editors/IProcessEditorModel';\n\n\ninterface IProcessVariableProps {\n    editable?: boolean;\n    screens: Array<IModel>;\n    modified: ModifiedCallbackType;\n    state: IProcessEditorState;\n}\n\nonBeforeMount(() => {\n    props.state.stepper.isTabValid = true\n})\n\nconst props = defineProps<IProcessVariableProps>();\nconst localization = container.resolve<ILocalization>(ILocalization);\nconst dataTypes = [\"String\", \"Boolean\", \"Screen\", \"Number\", \"Http Method\"];\nconst restMethods = [\"GET\", \"POST\", \"PUT\", \"PATCH\", \"DELETE\", \"HEAD\", \"CONNECT\", \"OPTIONS\", \"TRACE\"];\n\nconst newVariableName = ref(\"\");\nconst variableAlreadyExists = ref(false);\n\nconst tableHeaders = ref([\"parameter\", \"dataType\", \"currentValue\", \"defaultValue\", \"operations\"]);\nconst tableRows = ref(props.state.processVariablesTableRows.sort((variable1, variable2) => variable1.parameter.localeCompare(variable2.parameter)));\n\nfunction checkVariableNameExists() {\n    variableAlreadyExists.value = tableRows.value.find((row) => row.parameter == newVariableName.value) != undefined;\n}\n\nfunction addNewVariable() {\n    const variable: IProcessVariableRow = {\n        parameter: newVariableName.value,\n        currentValue: \"\",\n        dataType: \"String\",\n        defaultValue: \"\",\n        defaultDataType: \"\",\n        isDeletable: true,\n    };\n    if (variableAlreadyExists.value || newVariableName.value.length == 0) return;\n\n    tableRows.value.unshift(variable);\n    newVariableName.value = \"\";\n    props.modified({ model: true });\n}\n\nfunction deleteVariable(row: IProcessVariableRow, index: number) {\n    tableRows.value.splice(index, 1);\n    props.modified({ model: true });\n}\n\nfunction returnToDefaultValue(row: IProcessVariableRow) {\n    if(row.defaultDataType == \"Boolean\") {\n        row.currentValue = row.defaultValue == \"true\";\n    } else {\n        row.currentValue = row.defaultValue;\n    }\n    row.dataType = row.defaultDataType;\n    props.modified({ model: true });\n}\n\nfunction handleDataTypeChange(row: IProcessVariableRow) {\n    row.currentValue = \"\";\n    props.modified({ model: true });\n}\n\nfunction handleScreen($event: any, row: IProcessVariableRow) {\n    const selectedScreen = $event.target[$event.target.selectedIndex];\n    row.currentValue = selectedScreen.id;\n    props.modified({ model: true });\n}\n\nfunction modifiedModel() {\n    props.modified({ model: true });\n}\n</script>\n\n<style scoped>\n.title {\n    position: absolute;\n    top: 118px;\n    left: 32px;\n    font-size: 24px;\n    font-family: \"Roboto\";\n    color: #212121;\n}\n\n.variable-count {\n    position: absolute;\n    top: 128px;\n    left: 255px;\n    font-size: 14px;\n    font-family: \"Roboto\";\n    font-weight: 500;\n    color: #a1a1a1;\n}\n\n.new-variable-input {\n    position: absolute;\n    top: 118px;\n    left: 647px;\n    width: 220px;\n    height: 32px;\n    background: #ffffff 0% 0% no-repeat padding-box;\n    border: 1px solid #a1a1a1;\n    border-radius: 4px 0px 0px 4px;\n    padding: 4px 0px 7px 10px;\n}\n\n.new-variable-input::placeholder {\n    position: absolute;\n    color: #a1a1a1;\n}\n\n.plus-icon {\n    position: absolute;\n    color: white;\n    top: 3px;\n    left: 12px;\n    font-size: 18px;\n    user-select: none;\n}\n\n.add-new-text {\n    position: absolute;\n    left: 32px;\n    top: 7px;\n    font-size: 13px;\n    font-weight: 500;\n    user-select: none;\n}\n\n.new-variable-button {\n    position: absolute;\n    left: 866px;\n    top: 118px;\n    font-size: 14px;\n    background: #007c77 0% 0% no-repeat padding-box;\n    border-radius: 0px 4px 4px 0px;\n    width: 101px;\n    height: 32px;\n    font-family: \"Roboto\";\n    border: 0px solid;\n    color: white;\n    padding: 0px;\n    user-select: none;\n}\n\n.disabled {\n    opacity: 0.5;\n    user-select: none;\n    pointer-events: none;\n    cursor: default;\n}\n\n.data-table {\n    position: absolute;\n    top: 182px;\n    left: 32px;\n    width: 936px;\n    height: 556px;\n}\n\n.table-header-text {\n    top: 11px;\n    color: #373737;\n    font-size: 13px;\n    font-family: \"Roboto\";\n    font-weight: 500;\n}\n\n.table-header {\n    position: absolute;\n    top: 0px;\n    left: 0px;\n    width: 100%;\n    height: 40px;\n    background: #e3e3e3 0% 0% no-repeat padding-box;\n}\n\n.header-parameter {\n    position: absolute;\n    left: 12px;\n}\n\n.header-dataType {\n    position: absolute;\n    left: 193px;\n}\n\n.header-currentValue {\n    position: absolute;\n    left: 395px;\n}\n\n.header-defaultValue {\n    position: absolute;\n    left: 663px;\n}\n\n.table-body {\n    position: absolute;\n    top: 40px;\n    height: 528px;\n    width: 936px;\n    border: none;\n    overflow-x: hidden;\n    overflow-y: auto;\n}\n\n.table-body::-webkit-scrollbar {\n    width: 4px;\n}\n\n.table-body::-webkit-scrollbar-thumb {\n    background: #bcbcbc 0% 0% no-repeat padding-box;\n    border-radius: 10px;\n}\n\n.table-body::-webkit-scrollbar-thumb:hover {\n    background: #555;\n}\n\n.table-row {\n    width: 100%;\n    height: 44px;\n    display: flex;\n}\n\n.row-parameter {\n    margin-top: 14px;\n    margin-left: 12px;\n    font-size: 12px;\n    color: #212121;\n    font-family: \"Roboto\";\n    width: 140px;\n    display: inline-block;\n    white-space: nowrap;\n    overflow: hidden !important;\n    text-overflow: ellipsis;\n}\n\n.row-data-type {\n    margin-left: 41px;\n    font-size: 13px;\n    color: #212121;\n    font-family: \"Roboto\";\n    width: 154px;\n    display: inline-block;\n}\n\n.data-type {\n    margin-top: 12px;\n    white-space: nowrap;\n    overflow: hidden !important;\n    text-overflow: ellipsis;\n}\n\n.data-type-editable {\n    margin-top: 7px;\n}\n\n.data-type-combo {\n    width: 154px;\n    height: 30px;\n    background: #ffffff 0% 0% no-repeat padding-box;\n    border: 1px solid #a1a1a1;\n    border-radius: 4px;\n    padding-left: 6px;\n    font: normal normal normal 14px/19px Roboto;\n    color: #212121;\n}\n\n.current-value-input {\n    width: 220px;\n    height: 30px;\n    background: #ffffff 0% 0% no-repeat padding-box;\n    border: 1px solid #a1a1a1;\n    border-radius: 4px;\n    padding-left: 10px;\n    font: normal normal normal 14px/19px Roboto;\n    color: #212121;\n}\n\n.row-current-value {\n    margin-left: 48px;\n    font-size: 13px;\n    color: #212121;\n    font-family: \"Roboto\";\n    width: 220px;\n    display: inline-block;\n}\n\n.current-value {\n    margin-top: 12px;\n    white-space: nowrap;\n    overflow: hidden !important;\n    text-overflow: ellipsis;\n}\n\n.current-value-editable {\n    margin-top: 7px;\n}\n\n.row-default-value {\n    margin-left: 48px;\n    margin-top: 12px;\n    font-size: 13px;\n    color: #212121;\n    font-family: \"Roboto\";\n    width: 209px;\n    display: inline-block;\n    white-space: nowrap;\n    overflow: hidden !important;\n    text-overflow: ellipsis;\n}\n\n.row-operation {\n    margin-top: 11px;\n    margin-left: 12px;\n    font: normal normal normal 20px/25px Material Design Icons Desktop;\n    cursor: pointer;\n    display: inline-block;\n}\n</style>\n"],"sourceRoot":""}]);
